import {createRoot} from 'react-dom/client'
import { GameTestModel, GameTestComponent, getStorage, MockTelemetryLoggerFactory } from "clusterfun-client";
import TemplatoComponent from "./views/GameComponent";
export default TemplatoComponent;


const factory = new MockTelemetryLoggerFactory();

const gameTestModel = new GameTestModel(4, getStorage("clusterfun_test"), factory);

const rootContainer = document.getElementById('root') as HTMLElement;
const root = createRoot(rootContainer);
root.render( <GameTestComponent gameTestModel={gameTestModel} /> );            